	overflow: hidden;
	width: 100%;
	max-width: 100vw;
	position: relative;

	&__inner {
		padding: 104px 96px;
		display: grid;
		grid-template-areas:
			"address title"
			"links links";
		grid-template-rows: 320px auto;	
		grid-template-columns: 320px auto;	

	}

	&__address, .address {
		grid-area: address;
