			content: "";
			position: absolute;
			width: 33%;
			height: 2px;
			background-color: $white;
			z-index: 5;
			top: calc(100% - 2px);
			left: 180px;
		 }

	}

} 
.hero {
	width: 100%;
	height: 100%;
	max-width: 100vw;
	min-height: 100vh;
	
	&__inner {
		display: grid;
		grid-template: auto / 400px auto;
	}

	&__panel, .panel {
