			}

			&__button {
				margin-left: auto;
				cursor: pointer;
			}

		}

		&__slider, .slider {
			height: 100%;
			width: 100%;

			&__wrapper {
				display: flex;
				flex-direction: row;

			}

			&__image {
				display: block;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

		}

	}

