.swiper {
	width: 100%;
	height: 100%;
}

.swiper-slide {
	background-position: center;
	background-size: cover;
}

.swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.intro {

	&__image {
		display: block;
		width: 100%;
		clip-path: inset(0% 10px 10px 10px);
		transition: all 0.6s ease-in-out;
		clip-path: inset(0% 50% 0% 0%);
		overflow: hidden;
	}

}
.references {
  max-width: 100%;
  background-color: #655953;
  overflow: hidden;
  
  
  &__inner {
    display: grid;
    grid-template-areas:
		"heading heading"
    "description slider"
    "controls slider";
    grid-template-columns: 400px 100%;
    grid-template-rows: 300px auto 120px;
    min-height: 1120px;
		padding-bottom: 80px;
  };

  &__heading {
   grid-area: heading; 
	 position: relative;
	 margin-bottom: 64px;

	 &::after {
		content: "";
		position: absolute;
		width: 33%;
		height: 2px;
		background-color: $white;
		z-index: 5;
		top: calc(100% - 2px);
		left: 64px;
	 }

  }
  
  &__slider, .slider { 
    grid-area: slider;
    max-width: unset;
    width: 100%;
    
    &__container {
      height: 100%;
      display: flex;
    }
    
    &__wrapper {
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
    }
    
    &__item, .item {
      height: calc(100% - 80px);
      margin-left: 32px;
			width: 200px;
      margin-right: 0 !important;

      &__image {
        height: 100%;
        display: block;
        object-fit: cover;
      }

      &__heading {
        color: #FFFFFF;
				margin-top: 16px;
      }

      &:nth-child(2n) {
        margin-top: -80px;
      }

    }
    
  }

  &__controls, .controls {
    grid-area: controls;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: rgba(101, 89, 83, 0.95);
    z-index: 10;
		padding: 32px 64px;
    
    &__scrollbar {
      width: 100% !important;
      height: 4px;

      
      .swiper-scrollbar-drag {
        background: #FFFFFF;
      }
      
    }
    
    &__arrow-wrapper {
      margin-left: 16px;
      height: 16px;
      width: 48px;
      position: relative;
    }
    
    &__arrow {
      width: 16px;
      height: 16px;
      margin-top: unset;
      top: 0;
      
      &--prev {
        left: 0;
      }
      
      &--next {
        right: 0;
      }
      
      
      &::after {
        font-size: 16px;
        height: 16px;
        color: white;
      }

    }

  }
  
  &__description, .description {
    z-index: 10;
    width: auto;
    width: 100%;
    background-color: rgba(101, 89, 83, 0.95);
    grid-area: description;
    padding: 64px;
    
    &__text {
      color: white;
    }

  }

}

